
.parent-all-container{
   /* border: 2px solid violet; */
   height: 76vh;
   margin:auto;
   margin-bottom: 30px;
   width:100%;
   box-sizing: border-box;
}  
.parent-all-container * {
   font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
   text-align: center;
}
   
.main-container{
   /* border: 2px solid teal; */
   margin-top: 10px;
   /* border-top: 2px solid rgb(153, 153, 153); */
   width: 100%;
   height: 89%;
}
.quiz-container{
   float: left;
   display: inline;
   
   width: 65%;
   height: 100%;
   overflow: scroll;
   margin-left: 1%;
   box-shadow: 0px 0px 10px 5px rgb(202, 202, 202);
}

.botones{
   background-color: teal;
   /* box-shadow: 0px 0px 6px 3px teal; */
   margin: 0px 2%;
   margin-top:  10px;
   color: white;
   border:none;
   padding: 15px 20px;
   font-weight: bold;
   font-size: small;
   outline: none;
   padding: 10px 20px;

 }
 .botones:hover{
   cursor: pointer;
   
 }

