.quiz{
   display: block;
   padding: 20px;
   margin: 20px;
   width: 60%;
   margin-left: auto ;
   margin-right: auto;
   margin-bottom: 40px;
   /* border: 3px solid grey; */
   box-shadow: 0px 0px 10px 5px rgb(202, 202, 202);

}

.seleccion li {
   list-style: none;
   font-size: medium;
  
}
.seleccion span{
   margin-left: 30px;
   
}
.respuesta-correcta{
   
   margin-top: 15px;
   background-color: green;
   color: white;
   padding: 9px;
   /* padding-top: -5px ; */
   font-weight: bold;
   font-size: medium;
   /* display: n  one; */
}
.respuesta-correcta img {
   position: relative;
   top: 3px;
   margin-right: 10px;
   
}
.pregunta{
   font-size: medium;
   display: block;
   font-weight: bold;
   /* background-color: rgb(153, 153, 153); */
   color: teal;
   margin-bottom: 20px;
}
.numero-pregunta{
   display: block;
   margin-bottom: 10px;
   padding: 10px;
   font-size: medium;
   font-weight: bold;
   background-color: teal;
   color: white;
}
