.boton-cerrar{
   float: right;
   color: white;
   background-color: red;
   border: 2px solid red;
   outline: none;
   cursor: pointer;
   
}

.dialogo{
   outline: none;
   width: 40%;
   background-color: teal;
   color: white;
   border: 0px solid teal;
   box-shadow: 0px 0px 10px 5px grey
}
.dialogo * {
   font-weight: bold;
   outline: none;
   
}
.dialogo input {
   margin: 15px;
   width: 50%;
   padding: 3px;
   font-weight: normal;
}
.li-respuestas{
   overflow-y: scroll;
   height: 180px;
   /* background-color: white; */
   
}
.li-respuestas li {
   background-color: grey;
   margin: 13px;
   padding: 5px;
   list-style: none;
}
.button{
   cursor:pointer;
   background-color: white;
   border: 1px solid white;
   box-shadow: 0px 0px 1px 4px white;
   margin: 5px;
}
.titulo{
   margin-top:-5px;
   background-color: white;
   color: teal;
   padding: 5px;
   margin:0px
}